.media-content {
  padding-top: 32px;
}

.media-content__title {
  @include reset;
  margin-bottom: 28px;
  color: $dark-title;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .media-content {
    padding-top: 63px;
  }

  .media-content__title {
    margin-bottom: 34px;
    font-size: 30px;
  }
}

@media (min-width: $desktop-width) {
  .media-content {
    padding-top: 101px;
  }

  .media-content__title {
    margin-bottom: 36px;
  }
}
